body {
    overflow-x: hidden;
    font-family: Muli, sans-serif;
    font-size: 16px;
}
.nav li:hover{
    background-color: #6087d2;
}
a:active{
    background-color: #6087d2;
}
#viewport {
    padding-left: 250px;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
    font-family: Muli, sans-serif;
}

#content {
    width: 100%;
    position: relative;
    margin-right: 0;
}

/* Sidebar Styles */

#sidebar {
    z-index: 1000;
    position: fixed;
    left: 250px;
    width: 250px;
    height: 100%;
    margin-left: -250px;
    overflow-y: auto;
    background: #1E1A42;
    -webkit-transition: all 0.5s ease;
    transition: all 0.5s ease;
}

#sidebar header {
    background-color: #1E1A42;
    font-size: 20px;
    line-height: 52px;
    text-align: center;
}

#sidebar header a {
    color: #fff;
    display: block;
    text-decoration: none;
}

#sidebar header a:hover {
    color: #fff;
}

#sidebar .nav{

}

#sidebar .nav a{
    background: none;
    border-bottom: 1px solid #1E1A42;
    color: #CFD8DC;
    font-size: 14px;
    padding: 16px 24px;
}

#sidebar .nav a:hover{
    background: none;
    color: #ECEFF1;
}

#sidebar .nav a i{
    margin-right: 16px;
}
#filter-option {
    background-image: url(/nfq-task/static/media/logo.ee7cd8ed.svg);
}
#spec-title {
    border:none;
    background-image:none;
    background-color:transparent;
    box-shadow: none;
    -webkit-appearance: button;
    font-weight: bold;
    float: right;
    width: 90%;
    cursor: pointer;
    float: right;
}
.group{
    display: inline-block;
    margin-top: 7%;
    width: 110%;
    float: right;
}
select:focus, select{
    outline: none;
    background-color:transparent;
    box-shadow: none;
    -webkit-border-radius: 2px;
    -webkit-box-shadow: 0px 1px 3px rgba(0, 0, 0, 0.1);
    -webkit-padding-end: 20px;
    -webkit-padding-start: 2px;
    -webkit-user-select: none;
    background-position: center right;
    background-repeat: no-repeat;
    border: none;
    color: #6087d2;
    font-size: inherit;
    margin: 0;
    overflow: hidden;
    padding-top: 2px;
    padding-bottom: 2px;
    text-overflow: ellipsis;
    white-space: nowrap;
    font-family: 'Muli', sans-serif;
    float: right;

}
select:after{
    position: absolute;
    display: block;
    content: '';
    width: 10px; height: 10px;
    top: 50%; right: 25px;
    margin-top: -3px;
    border-bottom: 1px solid #fff;
    border-right: 1px solid #fff;
    -webkit-transform: rotate(45deg) translateY(-50%);
            transform: rotate(45deg) translateY(-50%);
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
    -webkit-transform-origin: 50% 0;
            transform-origin: 50% 0;
}
option{
    position: relative;
    display: inline;
    padding: 0 22px;
    border-bottom: 1px solid #b5b5b5;
    font-weight:lighter;
    color: #b5b5b5;
    line-height: 47px;
    cursor: pointer;
    -webkit-transition: all .4s ease-in-out;
    transition: all .4s ease-in-out;
}
.glyphicon-filter{
    color: #6087d2;
    margin-top:2%;
}
button{
    -moz-appearance: none;
    -webkit-appearance: none;
    border: none;
    background-color: transparent;
}
#served-btn{
    color: #332e7a;
    margin-left: 10%;
}
#served-btn:hover{
    color: #70d264;
}
.button-class{
    display: inline-block;
    float:bottom;
    margin-top: 12.5%;
    font-size: inherit;
}
.container-fluid{
    color: #6087d2;
    font-family: 'Muli', sans-serif;
    font-weight: bold;
    margin-right: 6.5em;

}
.nav-top{
    display: inline-block;
}

.save-span{
    margin-left: 20px;
    display: inline-block;
}
/* Modal */
.modal-content{
    font-family: 'Muli', sans-serif;
}
.modal-header, modal-header:after, modal-header:before{
    display: inline-block;
    width: 100%;
    height: 60px;
    margin-top: 0.2em;
}
.btn-link{
    float:right;
    font-weight: bold;
    color: #6087d2;
}
.modal-title{
    width: 60%;
    float:left;
    margin-bottom: 0;
    font-weight: bold;
    color: #333333;
}
.btn-primary{
    background-color: #6087d2;
    border:none;
    margin-top: 2%;
}
#menu{
    color: #CFD8DC;
}
body {
    font-family: 'lato', sans-serif;
    overflow: hidden;
    -webkit-overflow-scrolling: touch;
}
.container {
    margin-left: auto;
    margin-right: auto;
    padding-left: 10px;
    padding-right: 10px;

}
h2 {
    font-size: 26px;
    margin: 20px 0;
    text-align: center;
}
h2 small {
    font-size: 0.5em;
}
#list-1{
    background-color: #6087d2;
    color: #ECEFF1;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}
.table-row {

    border-radius: 3px;
    padding: 12px 20px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}
.table-header {
    color: #ECEFF1;
    border-radius: 3px;
    padding: 20px 20px;
    display: flex;
    justify-content: space-between;
    margin-bottom: 10px;
}
.responsive-table .table-header {
    background-color: #1e1a42;
    font-size: 14px;
    text-transform: uppercase;
    letter-spacing: 0.03em;
}
.responsive-table .table-row {
    background-color: #fff;
    box-shadow: 0px 0px 9px 0px #2c2874;
}

.responsive-table .col-1{
    flex-basis: 10%;
}
.responsive-table .col-2 {
    flex-basis: 40%;
}
.responsive-table .col-3 {
    flex-basis: 25%;
}
.responsive-table .col-4 {
    flex-basis: 25%;
}
@media all and (max-width: 767px) {
    .responsive-table .table-header {
        display: none;
    }
    .responsive-table li {
        display: block;

    }
    .responsive-table .col {
        flex-basis: 100%;
    }
    .responsive-table .col {
        display: flex;
        padding: 10px 0;
    }
    .responsive-table .col:before {
        color: #c9e2ff;
        padding-right: 10px;
        content: attr(data-label);
        flex-basis: 50%;
        text-align: right;
    }
}

body {
    overflow-x: hidden;
    font-family: Muli, sans-serif;
    font-size: 16px;
}
#find-form{
    position: absolute;
    display: inline-block;
    width: 700px;
    align-items: center;
    left: 50%;
    top:50%;
    -webkit-transform: translate(-50%,-50%);
            transform: translate(-50%,-50%);
    font-size: 1.5em;
}
@media screen and (min-width: 671px) {
    #find-form {
        font-size: 1.5em;
    }
}
@media screen and (max-width: 670px) {
    #find-form {
        font-size: 1em;
        width: 500px;
    }
    .btn-submit-number{
        -webkit-transform: scale(0.9);
                transform: scale(0.9);
    }
}
.label-qNumber{
